#name-header{
    transition: transform 0.5s ease-in, opacity 0.5s ease-in;
    transition-delay: 0.2s;
    opacity: 0;
    position: relative;
    top: -30px;
}


#name-header.fall{
    transform: translateY(30px);
    opacity : 1;
    position:relative;
}

#intro{
    transition: transform 0.5s ease-in, opacity 0.5s ease-in;
    opacity: 0;
    position: relative;
    top: -30px;
    font-size: 20px;
}

#intro.fall{
    transform: translateY(30px);
    opacity : 1;
    position:relative;
}

#description{
    transition: transform 0.5s ease-in, opacity 0.5s ease-in;
    transition-delay: 0.4s;
    opacity: 0;
    position: relative;
    top: -30px;
    font-size: 22px
}

#description.fall{
    transform: translateY(30px);
    opacity : 1;
    position:relative;
}

.img-bounce{
    transition: opacity 0.5s ease-in;
    transition-delay: 0.7s;
    position: relative;
    /* top: -30px; */
    opacity: 0;
}


.img-bounce.fall{
    opacity: 1;
    position: relative;
}


@keyframes bounce {
    0%, 20%, 50%, 80%, 100% {
    transform: translateY(0);
    }
    40% {
    transform: translateY(-18px);
    }
    60% {
    transform: translateY(-10px);
    }
}





@media (min-width: 2000px) {
    #intro{
      font-size: 25px;
    }
    #name-header{
        font-size: 100px;
    }
    #description{
        font-size: 28px;
    }
  }